import React from 'react';
import classnames from 'classnames';
import { AppstoreOutlined, BorderOutlined } from '@ant-design/icons';
import { useSystemStore } from '@/stores';
import styles from './index.less';

const Lifestyle = ({
  className,
}: any) => {
  const lifestyle = useSystemStore((state) => state.lifestyle);
  const toggleLifestyle = useSystemStore((state) => state.toggleLifestyle);
  return (
    <div className={classnames(className, styles.lifestyle)} onClick={() => toggleLifestyle(lifestyle === 'standard' ? 'minimalist' : 'standard')}>
      <div className={styles.icon}>
        {lifestyle === 'standard' ? <AppstoreOutlined /> : <BorderOutlined />}
      </div>
      <div className={styles.title}>
        {`${lifestyle === 'standard' ? 'Standard' :'Minimalist' } Lifestyle`}
      </div>
    </div>
  );
};

export default Lifestyle;
