import React from 'react';
import classnames from 'classnames';
import { ExpandOutlined, CompressOutlined } from '@ant-design/icons';
import { useSystemStore } from '@/stores';
import styles from './index.less';

const Fullscreen = ({
  className,
}: any) => {
  const fullscreen = useSystemStore((state) => state.fullscreen);
  const toggleFullScreen = useSystemStore((state) => state.toggleFullScreen);
  return (
    <div className={classnames(className, styles.fullscreen)} onClick={() => toggleFullScreen(!fullscreen)}>
      <div className={styles.icon}>
        {fullscreen ? (<CompressOutlined />) : (<ExpandOutlined />)}
      </div>
      <div className={styles.title}>{fullscreen ? 'Exit Fullscreen' : 'Enter Fullscreen'}</div>
    </div>
  );
};

export default Fullscreen;
