import React, { memo, useEffect } from 'react';
import ConfigProvider from '@/component/system/ConfigProvider';
import Desktop from '@/component/system/Desktop';
import Taskbar from '@/component/system/Taskbar';
import useTrackInIt from '@/hooks/useTrack';
import { useSystemStore } from '@/stores';

import APP from './app';

const Index = (): React.ReactElement => {
  useTrackInIt();

  const toggleTheme = useSystemStore((state) => state.toggleTheme);
  useEffect(() => {
    toggleTheme('light');
  }, []);

  return (
    <ConfigProvider prefixCls="eleven-os">
      <Desktop>
        <Taskbar />
        <div style={{ flex: 1 }}>
          <APP />
        </div>
      </Desktop>
    </ConfigProvider>
  );
};

export default memo(Index);
