import React from 'react';
import classnames from 'classnames';
import { useSystemStore } from '@/stores';
import { SunnyFilled, MoonFilled } from './Icon';
import styles from './index.less';

const Theme = ({
  className,
}: any) => {
  const theme = useSystemStore((state) => state.theme);
  const toggleTheme = useSystemStore((state) => state.toggleTheme);
  const isDark = theme === 'dark';
  return (
    <div className={classnames(className, styles.theme)} onClick={() => toggleTheme(theme === 'dark' ? 'light' : 'dark')}>
      <div className={classnames(styles.icon, isDark && styles.dark)}>
        {isDark ? (<MoonFilled />) : (<SunnyFilled />)}
      </div>
      <div className={styles.title}>{isDark ? 'Dark Mode' : 'Light Mode'}</div>
    </div>
  );
};

export default Theme;
