import React from 'react';
import classnames from 'classnames';
import { InteractionFilled, RightOutlined, RollbackOutlined } from '@ant-design/icons';
import { GiteeFilled, GitHubFilled } from './Icon';
import styles from './index.less';

const Warehouse = ({
  className,
}: any) => {
  return (
    <div className={classnames(className, styles.warehouse)}>
      <div className={styles.item} onClick={() => window.open('https://github.com/coolfishstudio/')}>
        <div className={styles.info}>
          <div className={styles.icon} style={{ backgroundColor: '#1F2328', color: '#fff', fontSize: '24px' }}>
            <GitHubFilled />
          </div>
          <div>
            <div className={styles.title}>GitHub</div>
            <div className={styles.desc}>coolfishstudio</div>
          </div>
        </div>
        <div className={styles.arrow}>
          <RightOutlined />
        </div>
      </div>
      <div className={styles.item} onClick={() => window.open('https://gitee.com/elevenOS')}>
        <div className={styles.info}>
          <div className={styles.icon} style={{ backgroundColor: '#C71D23', color: '#fff', fontSize: '18px' }}>
            <GiteeFilled />
          </div>
          <div>
            <div className={styles.title}>Gitee</div>
            <div className={styles.desc}>elevenos</div>
          </div>
        </div>
        <div className={styles.arrow}>
          <RightOutlined />
        </div>
      </div>
      <div
        className={styles.item}
        onClick={() => location.href = `${location.hostname.includes('gitee') ? 'https://coolfishstudio.github.io/' : 'https://elevenos.gitee.io/'}`}
      >
        <div className={styles.info}>
          <div className={styles.icon} style={{ backgroundColor: '#fff', color: '#13c2c2', fontSize: '36px' }}>
            <InteractionFilled />
          </div>
          <div>
            <div className={styles.title}>Switch</div>
            <div className={styles.desc}>{location.hostname.includes('gitee') ? 'github.io' : 'gitee.io'}</div>
          </div>
        </div>
        <div className={styles.arrow}>
          <RollbackOutlined />
        </div>
      </div>
    </div>
  );
};

export default Warehouse;
