import { StateCreator } from 'zustand';
import setCssVariables from '@/utils/setCssVariables';

const themeDark = {
  '--system-theme-color': '#fff',
  '--system-theme-color-hover': '#000',
  '--system-theme-bgcolor': 'rgba(31, 41, 55, 0.7)',
  '--system-theme-bgcolor-hover': 'rgba(255, 255, 255, 0.85)',
};

const themeLight = {
  '--system-theme-color': '#000',
  '--system-theme-color-hover': '#fff',
  '--system-theme-bgcolor': 'rgba(243, 244, 246, 0.7)',
  '--system-theme-bgcolor-hover': 'rgba(0, 0, 0, 0.85)',
};

export interface SystemSlice {
  theme: 'dark' | 'light';
  fullscreen: boolean;
  lifestyle: 'standard' | 'minimalist';
  toggleTheme: (theme: 'dark' | 'light') => void;
  toggleFullScreen: (v: boolean) => void;
  toggleLifestyle: (lifestyle: 'standard' | 'minimalist') => void;
};

export const createSystemSlice: StateCreator<SystemSlice> = (set) => ({
  theme: 'dark',
  fullscreen: false,
  lifestyle: 'standard',
  toggleTheme: (theme) => {
    setCssVariables(theme === 'dark' ? themeDark : themeLight);
    set(() => ({ theme }));
  },
  toggleFullScreen: (v) => {
    set(() => {
      return { fullscreen: v };
    });
  },
  toggleLifestyle: (lifestyle) => {
    set(() => ({ lifestyle }));
  },
});
